#section-4{
  margin-top: 0px;;
  display: flex;
  flex-direction: column;
  height: 200vh;
  width:90%;
}
#section-4-header{
    display: flex;
    flex-direction: row;
    color: white;
    padding-bottom: 20px;
}
#built-project-1{
  display:flex;
  flex-direction: row;
  position: relative;
  align-items: center;
  height:64vh;
  width: 100%;

}

#featured-p-image{
  position: relative;
  height: 100%;
  top: 0px;
  width: 62%;
  z-index: 200;

}

#data-chart {
  position:absolute;
  left:0;
  width:100%;
  height:100%;
  z-index: 100;
  border-radius: 5px;
}
#data-chart-background{
  position: absolute;
  background-color: rgba(255,228,0,0.85);
  z-index:300;
  width: 100%;
  height: 100%;
  border-radius:5px;
}

#data-chart-background:hover{
  background-color: rgba(255,228,0,0.0);
}
#featured-p-info{
  position: absolute;
  top: 40px;
  left:50%;
  width: 50%;
  z-index: 400;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  height: 200px;
  
}

.tools-list {
  display: flex;
  flex-direction: row;
}

.feature-project-button-list{
  display: flex;
  flex-direction: row;

}
#build-project-2{
  display:flex;
  flex-direction: row;
  position: relative;
  margin-top: 100px;
  height:64vh;

  width: 100%;
}

#featured-p2-image{
  position: relative;
  height: 100%;
  top: 0px;
  width: 62%;
  z-index: 200;
  left:38%;
  
}
#featured-p2-info{
  position: absolute;
  top: 20px;
  left:0;
  width: 50%;
  z-index: 400;
  display: flex;
  flex-direction: column;
  align-items: flex-start;

}

.feature-project-heading{
  font-family: Inconsolata, monospace;
  font-size: 14px;
  font-weight: 400;
  color: #ffe400;
}
.featured-project-heading-2{
  font-family: 'Calibre', 'Inter', 'San Francisco', 'SF Pro Text', -apple-system, system-ui, sans-serif;
  font-size: 20px;
  font-weight: 600;
  color:#ccd6f6;
  margin-top: 14px;
}

.featured-project-info {
  font-family: 'Calibre', 'Inter', 'San Francisco', 'SF Pro Text', -apple-system, system-ui, sans-serif;
  font-size: 15px;
  color:#8891b0;
  margin-top: 17px;
  padding: 15px;
  background-color: #1f2833;
  line-height: 22px;
  border-radius: 5px;
  text-align: right;
}


.featured-project-2-info {
  font-family: 'Calibre', 'Inter', 'San Francisco', 'SF Pro Text', -apple-system, system-ui, sans-serif;
  font-size: 15px;
  color:#8891b0;
  margin-top: 17px;
  padding: 15px;
  background-color: #1f2833;
  line-height: 22px;
  border-radius: 5px;
  text-align: left;
}

.tools-list{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding-top: 14px;
  font-family: 'SF Mono', 'Fira Code', 'Fira Mono', 'Roboto Mono', monospace;
  font-size: 14px;
  color: #8891b0;

  width: 60%;
  flex-wrap: nowrap; 
}
.tools-list-item{
  padding: 2px;
  flex-wrap: nowrap; 
  width: fit-content;
}

.feature-project-button-list{
  padding-top:4px;
}

#side-bar-buttons-linkedin-p1{
  color: #2eac43;
  padding-bottom:20px;
  transition: transform 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
  display: flex;
  flex-direction: column;
  width: 18px;
  height:18px;
  background-image: url('images/link.png') ;
  background-repeat: no-repeat;
  background-position: center; 
  background-size:contain;
  margin-right: 10px;
}

#side-bar-buttons-linkedin-p1:hover{
  transform: translateY(-4px);
  background-image: url('images/linkhover.png');
}

#side-bar-buttons-github-p1{
  color: #2eac43;
  padding-bottom:20px;
  transition: transform 0.4s cubic-bezier(0.34, 1.56, 0.64, 1);
  display: flex;
  flex-direction: column;
  width: 18px;
  height:18px;
  background-image: url('images/link.png') ;
  background-repeat: no-repeat;
  background-position: center; 
  background-size:contain;
  margin-right: 10px;
}

#side-bar-buttons-github-p1:hover{
  transform: translateY(-4px);
  background-image: url('images/linkhover.png');
}
